/*전체 폰트 설정*/
* {
  font-family: Pretendard;
  font-size: 100%;
}

/*포지션 설정*/
.wrap > div {
  position: relative;
}

/*PC에서 너비 1300px로 제한*/
.area {
  max-width: 1300px;
  margin: 0 auto;
}

/*각 페이지별 배경이미지*/

.hm_bg {
  background: url(image/mapBg.png) no-repeat center;
}

.design_bg {
  background: url(image/designBack.png) no-repeat center;
}

.cont_bg {
  background: url(image/contactBg.png) no-repeat center;
}

/* 문자 단위로 줄바꿈 */
.keep-all {
  word-break: keep-all;
}

/* 글줄 넘칠 시 말줄임 처리 */
.text-over-ellipsis {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4; /*보일 줄 수*/
  -webkit-box-orient: vertical;
}
